@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
:root {
  --scissors: hsl(39, 89%, 49%) to hsl(40, 84%, 53%);
  --paper: hsl(230, 89%, 62%) to hsl(230, 89%, 65%);
  --rock: hsl(349, 71%, 52%) to hsl(349, 70%, 56%);
  --lizard: hsl(261, 73%, 60%) to hsl(261, 72%, 63%);
  --cyan: hsl(189, 59%, 53%) to hsl(189, 58%, 57%);

  --dark-text: hsl(229, 25%, 31%);
  --score-text: hsl(229, 64%, 46%);
  --header-outline: hsl(217, 16%, 45%);
  --background: hsl(214, 47%, 23%) to hsl(237, 49%, 15%);
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow Semi Condensed', sans-serif;
}

body {
  background-color: var(--dark-text);
  background-image: linear-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  background-attachment: fixed;
  color: #ffffff;
}

/*
.App {
  overflow: auto;
}
*/

.container {
  margin: 0 auto;
  padding-top: 1px;
}

header {
  margin: 50px auto;
  width: 500px;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  border: 3px solid var(--header-outline);
  border-radius: 10px;
  padding: 20px;
}

.advanced-container header {
  margin-bottom: 0px;
}

.toggle {
  margin: auto;
}

.score-container {
  padding: 10px 40px;
  background-color: #ffffff;
  color: var(--dark-text);
  text-align: center;
  border-radius: 10px;
  font-weight: 700;
}

.label {
  font-size: 12px;
  letter-spacing: 3px;
  color: var(--score-text);
}

.score {
  font-size: 50px;
  color: var(--dark-text);
}

.placeholder {
  background-color: hsl(237, 49%, 15%);
  width: 280px;
  height: 280px;
  border-radius: 100%;
}

.results {
  margin: 0 100px;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-name: grow;
          animation-name: grow;
}

@-webkit-keyframes grow {
  from {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes grow {
  from {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.reset {
  background-color: #ffffff;
  color: var(--dark-text);
  border-radius: 10px;
  padding: 10px 40px;
  cursor: pointer;
  box-shadow: none;
  transition: 0.2s;
}

.reset:hover {
  box-shadow: 1px 1px 10px 5px white;
}

.reset.lose {
  color: hsl(349, 70%, 56%);
}

.reset.win {
  color: var(--dark-text);
}

.rules-overlay {
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.rules {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 30px;
  z-index: 2;
  position: relative;
}

.rules-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  color: var(--dark-text);
  font-weight: 700;
  margin-bottom: 40px;
}

.rules-header h2 {
  font-size: 1.8em;
  margin: 0;
}

.close-icon {
  cursor: pointer;
}

.rules-button {
  position: absolute;
  bottom: 20px;
  right: 20px;
  border: 1px solid #ffffff;
  border-radius: 10px;
  padding: 10px 40px;
  letter-spacing: 3px;
  font-size: 12px;
  transition: 0.2s;
  cursor: pointer;
}

.rules-button:hover {
  background-color: #ffffff;
  color: var(--dark-text);
}

@media screen and (max-width: 426px) {
  header {
    width: auto;
    width: initial;
    margin: 0 auto 20px;
    padding: 20px 20px 0;
  }

  .advanced-container header {
    margin-bottom: 20px;
  }

  .logo {
    width: 100px;
  }

  .score-container {
    padding: 10px;
  }

  .score {
    font-size: 30px;
  }

  .rules-button {
    position: static;
    max-width: 50px;
    margin: auto;
    text-align: center;
  }

  .overlay {
    background-color: #fff;
  }

  .rules {
    padding: 0;
    position: static;
    position: initial;
  }

  .rules-header {
    display: block;
    text-align: center;
  }

  .rules-img {
    width: 80%;
    margin: auto;
    display: block;
  }

  .close-icon {
    position: absolute;
    bottom: 50px;
    left: 0%;
    right: 0;
    margin: auto;
  }
}

.game-start {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.game-start.desktop {
  display: -webkit-flex;
  display: flex;
}

.game-start.mobile {
  display: none;
}

.players {
  padding-bottom: 10px;
  z-index: -1;
}

.user.show .game-button,
.computer.show .game-button {
  box-shadow: 0px 0px 0px 75px hsla(214, 47%, 23%, 80%),
    0px 0px 0px 150px hsla(214, 47%, 23%, 60%),
    0px 0px 0px 225px hsla(214, 47%, 23%, 40%);
}

.user.hide .game-button,
.computer.hide .game-button {
  box-shadow: inherit;
}

.game-start .title {
  margin-bottom: 50px;
  letter-spacing: 2px;
  word-spacing: 3px;
}

.game-start .icon-bg {
  padding: 60px;
}

.game-start .icon-bg img {
  width: 100px;
}

.game-start .game-button {
  border-width: 30px;
}

@media screen and (max-width: 1025px) {
  .game-start.desktop {
    display: none;
  }

  .game-start.mobile {
    display: block;
    -webkit-transform-origin: top left;
            transform-origin: top left;
    margin: auto;
  }

  .player-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
  }

  .game-start.mobile .players {
    padding: 0;
  }

  .game-start.mobile .user.show .game-button,
  .computer.show .game-button {
    box-shadow: 0px 0px 0px 25px hsla(214, 47%, 23%, 80%),
      0px 0px 0px 45px hsla(214, 47%, 23%, 60%),
      0px 0px 0px 75px hsla(214, 47%, 23%, 40%);
  }

  .game-start.mobile .title {
    font-size: 12px;
    margin: 20px auto;
  }

  .game-start.mobile .game-button {
    border-width: 10px;
  }

  .game-start.mobile .icon-bg {
    width: 50px;
    height: 50px;
    padding: 20px;
  }

  .game-start.game-start.mobile .icon-bg img {
    width: 40px;
  }

  .game-start.mobile .results {
    margin: auto auto 40px;
  }

  .game-start.mobile .results h1 {
    font-size: 3em;
    margin: 20px auto 10px;
  }

  .game-start.mobile .results .reset {
    width: 30%;
    margin: auto;
  }
}

.toggle {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px auto auto;
}

.toggle-icon {
  font-size: 1.5em;
}

.switch,
.toggle-icon {
  cursor: pointer;
}

.switch {
  width: 100px;
  height: 30px;
  border-radius: 15px;
  position: relative;
  border: 3px solid var(--header-outline);
  margin: 0 20px;
}

.switch-button {
  width: 30px;
  height: 30px;
  background-color: #ffffff;
  border-radius: 100%;
  box-shadow: 0px 0px 0px 10px var(--header-outline);
}

.toggle.easy .switch {
  background-color: hsl(214, 47%, 23%);
  -webkit-animation-name: advancedToEasyColor;
          animation-name: advancedToEasyColor;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.toggle.easy .switch-button {
  margin-left: 0px;
  -webkit-animation-name: advancedToEasyBtn;
          animation-name: advancedToEasyBtn;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.toggle.advanced .switch {
  background-color: hsl(237, 49%, 15%);
  -webkit-animation-name: easyToAdvancedColor;
          animation-name: easyToAdvancedColor;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.toggle.advanced .switch-button {
  margin-left: 70px;
  -webkit-animation-name: easyToAdvancedBtn;
          animation-name: easyToAdvancedBtn;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

@-webkit-keyframes easyToAdvancedColor {
  from {
    background-color: hsl(214, 47%, 23%);
  }

  to {
    background-color: hsl(237, 49%, 15%);
  }
}

@keyframes easyToAdvancedColor {
  from {
    background-color: hsl(214, 47%, 23%);
  }

  to {
    background-color: hsl(237, 49%, 15%);
  }
}

@-webkit-keyframes advancedToEasyBtn {
  from {
    margin-left: 70px;
  }

  to {
    margin-left: 0px;
  }
}

@keyframes advancedToEasyBtn {
  from {
    margin-left: 70px;
  }

  to {
    margin-left: 0px;
  }
}

@-webkit-keyframes advancedToEasyColor {
  from {
    background-color: hsl(237, 49%, 15%);
  }

  to {
    background-color: hsl(214, 47%, 23%);
  }
}

@keyframes advancedToEasyColor {
  from {
    background-color: hsl(237, 49%, 15%);
  }

  to {
    background-color: hsl(214, 47%, 23%);
  }
}

@-webkit-keyframes easyToAdvancedBtn {
  from {
    margin-left: 0px;
  }

  to {
    margin-left: 70px;
  }
}

@keyframes easyToAdvancedBtn {
  from {
    margin-left: 0px;
  }

  to {
    margin-left: 70px;
  }
}

.game-buttons-wrapper {
  width: 400px;
  height: 400px;
  position: relative;
  margin: 0 auto;
}

.game-buttons-wrapper.advanced {
  width: 450px;
  height: 450px;
}

.game-bg {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
}

.game-button {
  border-radius: 100%;
  border-width: 20px;
  border-style: solid;
  border-color: transparent;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #ffffff;
  background-origin: border-box;
  transition: 0.2s;
  cursor: pointer;
}

.game-button.advanced {
  border-width: 15px;
}

.choose .game-button {
  position: absolute;
}

.choose .game-button:hover {
  box-shadow: 1px 1px 10px 5px white;
}

.icon-bg {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #ffffff;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  padding: 10px;
  box-shadow: inset 0px 8px 0px 0px hsl(0, 0%, 80%);
}

.advanced .icon-bg {
  padding: 20px;
  width: 60px;
  height: 60px;
}

.rock {
  background-image: linear-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
  box-shadow: 0px 7px 0px 0px hsl(349, 70%, 45%);
}

.choose .rock {
  bottom: 10px;
  left: 120px;
}

.choose .rock.advanced {
  bottom: 10px;
  left: auto;
  left: initial;
  right: 70px;
}

.paper {
  background-image: linear-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
  box-shadow: 0px 7px 0px 0px hsl(230, 89%, 45%);
}

.choose .paper {
  top: 0;
  left: 0;
}

.choose .paper.advanced {
  top: 140px;
  left: auto;
  left: initial;
  right: 0px;
}

.scissors {
  background-image: linear-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
  box-shadow: 0px 7px 0px 0px hsl(40, 84%, 45%);
  top: 0;
  right: 0;
}

.choose .scissors {
  top: 0;
  right: 0;
}

.choose .scissors.advanced {
  top: 30px;
  right: auto;
  right: initial;
  left: 170px;
}

.lizard {
  background-image: linear-gradient(hsl(261, 73%, 60%), hsl(261, 72%, 63%));
  box-shadow: 0px 7px 0px 0px hsl(261, 72%, 45%);
}

.choose .lizard {
  bottom: 10px;
  left: 70px;
}

.spock {
  background-image: linear-gradient(hsl(189, 59%, 53%), hsl(189, 58%, 57%));
  box-shadow: 0px 7px 0px 0px hsl(189, 58%, 45%);
}

.choose .spock {
  top: 140px;
  left: 0px;
}

@media screen and (max-width: 426px) {
  .game-buttons-wrapper,
  .game-buttons-wrapper.advanced {
    width: 300px;
    height: 300px;
    margin-bottom: 50px;
  }

  .game-bg {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  .choose .game-button {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }

  .choose .rock {
    left: 25%;
    bottom: 0px;
  }

  .choose .paper {
    top: -30px;
    left: -20px;
  }

  .choose .scissors {
    top: -30px;
    right: -20px;
  }

  .choose .scissors.advanced {
    top: -20px;
    left: 90px;
  }

  .choose .paper.advanced {
    top: 70px;
    right: -20px;
  }

  .choose .rock.advanced {
    bottom: -30px;
    right: 20px;
  }

  .choose .lizard.advanced {
    bottom: -30px;
    left: 20px;
  }

  .choose .spock.advanced {
    top: 70px;
    left: -20px;
  }
}

